@keyframes slide-up {
    from {
        transform: translateY(100%);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.animate-slide-up {
    animation: slide-up 1s ease-out forwards;
}


@keyframes slide-down {
    from {
        transform: translateY(-100%);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.animate-slide-down {
    animation: slide-down 0.5s ease-out forwards;
}



/* Dashboard.css */

/* Loader container */
.loader {
    border: 8px solid rgba(0, 0, 0, 0.1);
    /* Light background */
    border-left-color: rgb(249 115 22 / var(--tw-bg-opacity));
    /* Primary color for the spinning effect */
    border-radius: 50%;
    /* Make it circular */
    width: 50px;
    /* Loader width */
    height: 50px;
    /* Loader height */
    animation: spin 1s linear infinite;
    /* Spin animation */
    display: flex;
    /* Flex to center */
    justify-content: center;
    /* Center content */
    align-items: center;
    /* Center content */
}

/* Spin animation */
@keyframes spin {
    0% {
        transform: rotate(0deg);
        /* Start rotation */
    }

    100% {
        transform: rotate(360deg);
        /* End rotation */
    }
}